export const savePetsToCustomer = (pet: Pet): HoliThunk => (
  dispatch,
  getStore
) => {
  const pets = getStore().PetsReducer.pets;
  firebase
    .functions()
    .httpsCallable("CALL-savePet")({
      pet,
    })
    .then((response) => {
      console.log(response);
    })
    .catch((error) => {
      console.log(error);
    });
};

export const savePendingPet = (pet: Pet): HoliThunk => async (dispatch) => {
  dispatch({
    type: StoreActions.SavePendingPet,
    payload: pet,
  });
};

